<template>
  <div class="editContacts">
    <van-nav-bar title="编辑地址" left-arrow>
      <van-icon name="edit" slot="right" />
    </van-nav-bar>

    <van-address-edit
      :area-list="areaList"
      show-delete
      show-set-default
      show-search-result
      :search-result="searchResult"
      @save="onSave"
      @delete="onDelete"
      @change-detail="onChangeDetail"
    />
  </div>
</template>

<script>
  export default {
    data() {
      return {
        areaList:{
          province_list: {
            430000: '湖南省'
          },
          city_list: {
            431100: '永州市'
          },
          county_list:{
            431102: '零陵区',
            431103: '冷水滩区',
            431121: '祁阳县',
            431122: '东安县',
            431123: '双牌县',
            431124: '道县',
            431125: '江永县',
            431126: '宁远县',
            431127: '蓝山县',
            431128: '新田县',
            431129: '江华瑶族自治县',
          }
        },
        searchResult: []
      }
    },

    methods: {
      onSave() {
        Toast('save');
      },
      onDelete() {
        Toast('delete');
      },
      onChangeDetail(val) {
        if (val) {
          this.searchResult = [{
            name: '黄龙万科中心',
            address: '杭州市西湖区'
          }];
        } else {
          this.searchResult = [];
        }
      }
    }
  }
</script>



